









































import Vue from "vue";
import Component from "vue-class-component";
import Swipe from "../../components/Swipe.vue";
import mixin from "../../config/mixin";
import comhead from "../../components/Comhead.vue";
import { List } from 'vant';
@Component({
  components: {
    Swipe,
    comhead
  },
  mixins: [mixin]
})


export default class ProductList extends Vue {

  secCategoryItem = {
  };
  list= [];
  loading=false;
  finished= false;
  selectIndex="";
  catlist="";
  goodsList = [];
  catId='';
  parentCatId="";
  pageIndex=0;
  title="";
  goProductDetail(goodsId) {
    this.$router.push({
      path: "/productdetail",
      query: {
        goodsId: goodsId
      }
    });
  }
  selectCatItem(index){
    let item = this.catlist[index];
    if(item['catId']!=this.catId){
      this.goodsList = []
      this.pageIndex = 0;
    }
    this.catId = item['catId'];
    this.getProductList();    
    this.$router.replace({
      path: "/productlist",
     query:{
       catId:this.catId,
       parentCatId:this.parentCatId,
       selectIndex:index,
       title:this.title
       }
    });
  }
  getProductList() {
    Vue.prototype.$reqFormPost(
      "/user/goods/list",
      {
        catId: this.catId,
        page:this.pageIndex,
        pageSize:20,
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          return;
        }        
        if(this.pageIndex==0){
          this.goodsList = res.data.data.goodsList;
        }else{
          for (let i = 0; i < res.data.data.goodsList.length; i++) {
            this.goodsList.push(res.data.data.goodsList[i])
          }
        }
        
      }
    );
  }
  // 下拉
  loadMore(){
    console.log('loadMore');
      setTimeout(() => {
        this.pageIndex++;
        this.getProductList();
        console.log('pageIndex',this.pageIndex)
      }, 200);
  }
  onLoad() {
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        this.loading = false;

        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
    }
  getCategoryList(){
     Vue.prototype.$reqFormPost("/user/cat/list", {
      parentId:  this.parentCatId
     }, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        return;
      }
      this.catlist = res.data.data
      console.log("getCategoryList",res.data.data);
    });
      
    }

  mounted() {
   
   //load products
    this.catId = this.$route.query.catId;
    this.getProductList();

    //load categories
    this.parentCatId = this.$route.query.parentCatId;
    this.getCategoryList();

    //load selectIndex
    this.selectIndex = this.$route.query.selectIndex;

    //load title
    this.title = this.$route.query.title;
   
  }

  handlePX(CssName, PxNumber) {
    return CssName +":" +this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /750 * PxNumber +"px;";
  }
}



